<template>
  <div>
    <el-button type="primary" size="mini" round @click="handleAdd"
      >新建</el-button
    >
    <el-dialog :title="'新建' + titleName" :visible.sync="dialogFormVisible">
      <el-form
        :model="projectform"
        :rules="rules"
        ref="projectform"
        label-width="80px"
        label-position="right"
      >
        <!-- <div>{{ titleName }}</div> -->
        <el-form-item label="名称" prop="name">
          <!-- :disabled="true" -->
          <el-input v-model="projectform.name" style="width: 220px"> </el-input>
        </el-form-item>
        <el-form-item label="创建方式" prop="mode">
          <!--  @change="changeMode" -->
          <el-radio-group v-model="projectform.mode">
            <el-radio label="onenew">创建新的数据集</el-radio>
            <el-radio label="inherit">导入已有的数据集</el-radio>
          </el-radio-group>
        </el-form-item>
        <!--   :disabled="projectform.isdisabled" -->
        <el-form-item
          label="数据集"
          :prop="projectform.mode === 'inherit' ? 'inherit' : ''"
          v-if="projectform.mode == 'inherit'"
        >
          <el-select
            v-model="projectform.inherit"
            remote
            clearable
            loading-text
            filterable
            :remote-method="getRemoteData"
            placeholder="请选择"
            @clear="clearInherit"
          >
            <el-option
              v-for="item in projectform.options"
              :key="item.dataSetName"
              :label="item.dataSetName"
              :value="item.dataSetName"
            >
            </el-option>
          </el-select>

          <!-- <el-select
            v-show="projectform.mode == 2"
            v-model="projectform.value"
            placeholder="请选择已有数据集"
            @change="changeSelect"
          >
            <el-option
              v-for="item in projectform.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select> -->
        </el-form-item>
        <el-form-item label="说明备注" prop="desc">
          <el-input type="textarea" v-model="projectform.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <!-- :disabled="traindisabled" -->
          <el-button
            type="primary"
            size="mini"
            @click="handleData('projectform')"
            :disabled="projectform.istrue"
            >开始创建</el-button
          >
          <el-button
            type="warning"
            size="mini"
            @click="resetForm('projectform')"
            >重置</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 表单数据
      projectform: {
        name: "",
        mode: "onenew",
        options: [],
        value: "",
        inherit: "",
        // isdisabled: false, //控制下列选项的禁用
        desc: "",
        // istrue: false, //控制创建按钮的禁用
      },
      dialogFormVisible: false,
      titleName: "", //新建组件动态显示名称
      // 下拉参数
      resultList: [],
      length: "", //下拉参数的长度
      isNext: false, //滚动加载标志
      pageNum: 1, //分页页数
      totalPages: "",
      currentPage: 1,
      // 校验规则
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 10个字符", trigger: "blur" },
        ],
        mode: [
          {
            required: true,
            message: "请至少选择一个创建模式",
            trigger: "change",
          },
        ],
        inherit: [
          { required: true, message: "请选择继承数据集", trigger: "blur" },
        ],
        desc: [{ required: true, message: "请填写描述信息", trigger: "blur" }],
      },

      // 新建数据集的post参数
      dateType: "",
      inheritDataSet: "",
    };
  },
  computed: {
    // 获取当前新建的数据集类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    // 获取二级菜单栏信息 赋值给新建导入数据集的下拉菜单
    menuList() {
      return this.$store.state.ItemDetails.menuList;
    },
    // 获取选中二级菜单栏信息
    // itemData() {
    //   return this.$store.state.ItemDetails.itemData;
    // },
  },
  watch: {
    currentType: {
      handler(newVal) {
        // console.log("类型改变了" + newVal);
        this.titleName = "创建新" + this.currentType + "数据集";
        // this.getParam();
      },
      deep: true,
    },
  },
  methods: {
    // 远程搜索
    getRemoteData(keyword) {
      console.log(keyword);
      this.$api
        .searchData({
          keyword: keyword,
          pageSize: 18,
          pageNum: 1,
          dataType: this.currentType,
        })
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            console.log(res);
            this.projectform.options = res.mapMsg;
          } else if (res.code == 401) {
            this.projectform.options = "";
          }
        })
        .catch((res) => {
          console.log(res);
        });
    },
    // 清空继承的值时 获取下拉菜单参数
    clearInherit() {
      this.$api
        .resultListV2({
          pageNum: 1,
          pageSize: 25,
          dataType: this.currentType,
        })
        .then((res) => {
          console.log(res);
          this.projectform.options = res.mapMsg;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 点击创建按钮
    handleAdd() {
      this.dialogFormVisible = true;
      this.getRemoteData();
      // this.getParam();
      // this.getParamv1();
      if (this.$refs["projectform"] !== undefined) {
        this.resetForm("projectform");
      }
      if (this.currentType == "image") {
        this.titleName = "图片";
      } else if (this.currentType == "text") {
        this.titleName = "文本";
      } else if (this.currentType == "video") {
        this.titleName = "视频";
      } else if (this.currentType == "audio") {
        this.titleName = "音频";
      }
    },
    // 创建数据集
    handleData(formName) {
      this.inheritDataSet = this.projectform.inherit;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert("submit!");
          console.log(this.currentType, this.inheritDataSet);
          this.$api
            .createDataset({
              dataSetName: this.projectform.name,
              dataType: this.currentType,
              desc: this.projectform.desc,
              inheritDataSet: this.inheritDataSet,
            })
            .then((res) => {
              console.log(res);
              if (res.code === 401) {
                this.$message({
                  type: "error",
                  message: "该数据集已存在",
                  showClose: true,
                });
                // alert("该数据集已存在");
                this.projectform.name = "";
              } else if (res.code === 200) {
                this.dialogFormVisible = false;
                this.$message({
                  type: "success",
                  message: "创建成功",
                  showClose: true,
                });
                // 定义一个对象字符串;
                let str = {
                  desc: this.projectform.desc,
                  id: this.menuList.length - 1,
                  dataSetName: this.projectform.name,
                  dataType: this.currentType,
                };
                this.$store.commit("ItemDetails/add_resultList_isAdd", str);
                // this.$emit("changeResult", this.projectform.name);

                // this.$store.commit("ItemDetails/appendAarry", str);
              }
            });
        }
        // else {
        //   this.projectform.istrue = true;
        //   console.log("error submit!!");
        //   return false;
        // }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted() {
    this.clearInherit();
    // this.getParam();
  },
};
</script>
<style lang="scss" scoped></style>
