<template>
  <div>
    <el-button type="primary" size="mini" round @click="handleDowmload()"
      >下载</el-button
    >
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      //   dialogFormVisible: false,
    };
  },
  computed: {
    currentName() {
      return this.$store.state.ItemDetails.itemDate;
    },
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
  },
  methods: {
    handleDowmload() {
      console.log("开始下载");
      console.log(this.currentName, this.currentType);

      if (this.currentType == "text") {
        const name = this.currentName + ".csv";
        axios({
          methods: "get",
          url: "http://192.168.10.154:8000/MoFarmBackEnd/Data/download_dataset/",
          params: { dataSetName: this.currentName },
          responseType: "blob",
          headers: {
            "Content-Type": "application/octet-stream",
          },
        }).then((res) => {
          console.log(res.data);
          const dataBlob = new Blob([`\ufeff${res.data}`], {
            type: "application/octet-stream", //根据后端返回的请求头的格式去设定 也可以在headers中Content-Type设定
          }); //返回的格式
          const url = window.URL.createObjectURL(dataBlob);
          console.log(url);
          var aLink = document.createElement("a");
          aLink.style.display = "none";
          aLink.href = url;
          aLink.setAttribute("download", name);
          document.body.appendChild(aLink);
          aLink.click();
          document.body.removeChild(aLink); //下载完成移除元素
        });
      } else if (this.currentType != "text") {
        const name = this.currentName + ".zip";
        axios({
          methods: "get",
          url: "http://192.168.10.154:8000/MoFarmBackEnd/Data/download_dataset/",
          params: { dataSetName: this.currentName },
          responseType: "blob",
          headers: {
            "Content-Type": "application/zip",
          },
        })
          .then((res) => {
            console.log(res);

            let blobfile = new Blob([res.data], {
              type: "application/zip", //根据后端返回的请求头的格式去设定 也可以在headers中Content-Type设定
            });
            // let blobfile = res;
            console.log(typeof blobfile);

            var url = window.URL.createObjectURL(blobfile);
            console.log(url);
            var aLink = document.createElement("a");
            aLink.style.display = "none";
            aLink.href = url;
            aLink.setAttribute("download", name);
            document.body.appendChild(aLink);
            aLink.click();
            document.body.removeChild(aLink); //下载完成移除元素
            window.URL.revokeObjectURL(url); //释放掉blob对象
          })
          .catch((error) => this.$message.error(error));
      }

      // this.$api
      //   .downLoad({
      //     params: { dataSetName: this.currentName },
      //     responseType: "arraybuffer",
      //     // responseType: "blob",
      //     // responseType: "blob",
      //     // headers: { "Content-Type": "application/octet-stream" },
      //   })
      //   .then((res) => {
      //     console.log(typeof res);

      //     let blobfile = new Blob([res], {
      //       // type: "application/octet-stream",
      //       type: "application/zip",
      //     });
      //     // let blobfile = res;
      //     console.log(typeof blobfile);

      //     var url = window.URL.createObjectURL(blobfile);
      //     var aLink = document.createElement("a");
      //     aLink.style.display = "none";
      //     aLink.href = url;
      //     aLink.setAttribute("download", name);
      //     document.body.appendChild(aLink);
      //     aLink.click();
      //     document.body.removeChild(aLink); //下载完成移除元素
      //     window.URL.revokeObjectURL(url); //释放掉blob对象
      //   });
    },
  },
};
</script>
<style lang="scss" scoped></style>
